import React, { PureComponent } from 'react';
import Star from './star/Star';
import MeteorRain from './star/MeteorRain';

import moonImg from './assets/moon.jpg';
import github from './assets/github.png';

import './app.css'

class App extends PureComponent {
  constructor(props) {
      super(props);
      this.context = null;
      this.starts = [];
      this.starCount = 800;
      this.rains = [];
      this.rainCount = 20;
  }

  componentDidMount() {
    this.initCanvas();

    for (let i = 0; i < this.starCount; i++) {
      let star = new Star();
      star.init();
      star.draw(this.context);
      this.starts.push(star);
    }

    //画流星
    for (let i = 0; i < this.rainCount; i++) {
      let rain = new MeteorRain();
      rain.init();
      rain.draw(this.context);
      this.rains.push(rain);
    }

    this.drawMoon();//绘制月亮
    this.playStars();//绘制闪动的星星
    this.playRains();//绘制流星
  }

  // 初始化画布
  initCanvas() {
    let canvas = document.getElementById('canvas');
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    this.context = canvas.getContext('2d');
  }

  //星星闪起来
  playStars() {
    for (let n = 0; n < this.starCount; n++){
      this.starts[n].getColor();
      this.starts[n].draw(this.context);
    }

    setTimeout(() => {
        this.playStars();
    }, 100);
  }

  // 绘制流星
  playRains() {
    for (let n = 0; n < this.rainCount; n++) {
      let rain = this.rains[n];
      rain.move(this.context);//移动
      if (rain.y > window.innerHeight) {//超出界限后重来
        this.context.clearRect(rain.x, rain.y - rain.height, rain.width, rain.height);
        this.rains[n] = new MeteorRain();
        this.rains[n].init();
      }
    }
    setTimeout(() => {
      this.playRains()
    }, 2);
  }

  // 画月亮
  drawMoon(){
    let moon = new Image();
    moon.src = moonImg;
    this.context.drawImage(moon, -5, -10);
  }

  render() {
    return (
      <div>
        <canvas className="bg" id="canvas"/>
          <a className="github"
             href="https://github.com/liihuu"
             rel="noopener noreferrer" target="_blank">
              <img className="img" src={github}/>
              <p className="name">liihuu</p>
          </a>
      </div>

    )
  }
}

export default App;
