class MeteorRain {
    constructor() {
        this.x = -1;
        this.y = -1;
        this.length = -1;//长度
        this.angle = 30; //倾斜角度
        this.width = -1;//宽度
        this.height = -1;//高度
        this.speed = 1;//速度
        this.offsetX = -1;//横轴移动偏移量
        this.offsetY = -1;//纵轴移动偏移量
        this.alpha = 1; //透明度
        this.color1 = "";//流星的色彩
        this.color2 = "";  //流星的色彩
    }

    //初始化
    init() {
        this.getPos();
        this.alpha = 1;//透明度
        this.getRandomColor();
        //最小长度，最大长度
        let x = Math.random() * 80 + 100;
        this.length = Math.ceil(x);
        this.angle = 30; //流星倾斜角
        x = Math.random() + 0.5;
        this.speed = Math.ceil(x); //流星的速度
        let cos = Math.cos(this.angle * 3.14 / 180);
        let sin = Math.sin(this.angle * 3.14 / 180) ;
        this.width = this.length * cos ;  //流星所占宽度
        this.height = this.length * sin ;//流星所占高度
        this.offsetX = this.speed * cos ;
        this.offsetY = this.speed * sin;
    }

    /**************获取随机颜色函数*****************/
    getRandomColor(){
        let a = Math.ceil(255 - 240 * Math.random());
        //中段颜色
        this.color1 = "rgba("+ a + "," + a + "," + a + ",1)";
        //结束颜色
        this.color2 = "black";
    }


    /***************重新计算流星坐标的函数******************/
    countPos() {
        //往左下移动,x减少，y增加
        this.x = this.x - this.offsetX;
        this.y = this.y + this.offsetY;
    }

    /*****************获取随机坐标的函数*****************/
    getPos() {
        //横坐标200--1200

        this.x = Math.random() * window.innerWidth; //窗口高度
        //纵坐标小于600
        this.y = Math.random() * window.innerHeight;  //窗口宽度
    }

    /****绘制流星***************************/
    //绘制一个流星的函数
    draw(context) {
        context.save();
        context.beginPath();
        context.lineWidth = 1; //宽度
        context.globalAlpha = this.alpha; //设置透明度
        //创建横向渐变颜色,起点坐标至终点坐标
        let line = context.createLinearGradient(this.x, this.y,
            this.x + this.width,
            this.y - this.height);



        //分段设置颜色
        line.addColorStop(0, "white");
        line.addColorStop(0.3, this.color1);
        line.addColorStop(0.6, this.color2);
        context.strokeStyle = line;
        //起点
        context.moveTo(this.x, this.y);
        //终点
        context.lineTo(this.x + this.width, this.y - this.height);
        context.closePath();
        context.stroke();
        context.restore();
    }

    move(context) {
        //清空流星像素
        let x = this.x + this.width - this.offsetX;
        let y = this.y - this.height;
        context.clearRect(x - 3,y - 3,this.offsetX + 5,this.offsetY + 5);
        //重新计算位置，往左下移动
        this.countPos();
        //透明度增加
        this.alpha -= 0.002;
        //重绘
        this.draw(context);
    }
}

export default MeteorRain