class Star {
    constructor() {
        this.x = window.innerWidth * Math.random();//横坐标
        this.y = 5000 * Math.random();//纵坐标
        this.text=".";//文本
        this.color = "white";//颜色
    }

    getColor() {
        let r = Math.random();

        if(r < 0.5){
            this.color = "#333";
        } else {
            this.color = "white";
        }
    }

    init() {
        this.getColor();
    }

    //绘制
    draw(context) {
        context.fillStyle = this.color;
        context.fillText(this.text, this.x, this.y);
    }
}

export default Star